import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { AddUsersComponent } from './components/add-users/add-users.component';
import { UnapprovedComponent } from './components/unapproved/unapproved.component';
import { ApprovedComponent } from './components/approved/approved.component';
import { DeActivatedComponent } from './components/de-activated/de-activated.component';
import { UserReportsComponent } from './components/user-reports/user-reports.component';
import { UserReportDataComponent } from './components/user-reports/user-report-data/user-report-data.component';
import { SignuprequestComponent } from './components/signuprequest/signuprequest.component';

import { ProfileResolversService } from 'src/app/modules/profile/profile-resolvers.service';

export const routes: Routes = [

	{ path: '', redirectTo: 'unapproved', pathMatch: 'full' },
	{ path: 'usersignuprequest', component: SignuprequestComponent },
	{
		path: 'unapproved', component: UnapprovedComponent,
		children: [
			{ path: 'profile',
			outlet: "profile",
			runGuardsAndResolvers: 'always',
			resolve    : {
				initialData: ProfileResolversService
			},  
			loadChildren: () => import('src/app/modules/profile/profile-area.module').then(m => m.ProfileAreaModule) },
		]

	},
	{ path: 'add-users', component: AddUsersComponent },
	{ path: 'edit-user/:id', component: AddUsersComponent },
	{
		path: 'approved', component: ApprovedComponent

	},
	{ path: 'access-control', component: AddUsersComponent },
	{ path: 'broadcast', component: AddUsersComponent },
	{ path: 'statistics', component: AddUsersComponent },
	{ path: 'de-activated', component: DeActivatedComponent },
	{ path: 'reports', component: UserReportsComponent },
	{ path: 'reports/:name/:id', component: UserReportDataComponent },
	{ path: '**', redirectTo: 'unapproved', pathMatch: 'full' },

]

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class UnitUsersRoutingModule { }
