import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-approved',
  templateUrl: './approved.component.html',
  styleUrls: ['./approved.component.scss']
})
export class ApprovedComponent implements OnInit {

  totalUnits:any;
  totalUsers:number = 0;
  unitData:string = "";
  approvedUsersData:any;
  rawApprovedUsersData:any;
  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  selectedTab:number = -1;

  isUserDataLoaded:boolean = false;

  isProfile:boolean = false;
  viewUserId:any;
  unitUserId:any;
  towerList:Array<any>=[];
  apartmentBlockId:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private sharedService:SharedService,
    private sessionService:SessionService
  ) { }

  showApprovedUserDetails(item,index) {
    let user = item.userInfo[index];
    if(!this.isMobileView()){
      if(this.selectedTab != user.userId){
        this.selectedTab = user.userId;
      }
      else{
        this.selectedTab = -1;
      }
    }
    else {
      this.router.navigate(['ams/approved-user-details/'+{id : user.userId , unitUserId:item.apartmentBlockUnitUserId}]);
    }

    this.viewUserId = user.userId;  	  
    this.unitUserId = item.apartmentBlockUnitUserId;
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex; 
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  isMobileView() {
    return window.innerWidth <= 991;
  }

  getBlockNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.approvedUsersData[index].blockNo = data[0].apartmentBlockNumber;
      return data[0].apartmentBlockNumber;
    }
    
  }

  getBlockUnitNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.approvedUsersData[index].unitNo = data[0].apartmentBlockUnitNumber;
      return data[0].apartmentBlockUnitNumber;
    }
    
  }

  isActive(num){
    return this.selectedTab == num ? 'active' : '';
  }

  getParams(event){
    this.isProfile = event;
  }

  showUserInfo(event){
    
    this.isProfile = event;

    // setTimeout(() => {

    //   this.isProfile = event;

    // }, 10);

    setTimeout(() => {

      var elem = document.querySelector('.user-info-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

    this.router.navigate(['ams/unit&users/approved/basic',this.viewUserId]);
  }


  filterItem(value){
    if(!value){
        this.getApprovedUsers();
    }else{
      this.approvedUsersData = this.rawApprovedUsersData.filter(key =>{
       return key.userInfo.filter(item =>{
          item.userName.toLowerCase().indexOf(value.toLowerCase()) > -1
        })
      })
      // this.approvedUsersData = Object.assign([], this.rawApprovedUsersData).filter(item => 
      //   item.userInfo.filter(key => 
      //     key.userName.toLowerCase().indexOf(value.toLowerCase()) > -1))
    }
  }

 getApprovedUsers(){
  let approvedUsersParam ={
    apartmentId: parseInt(this.sessionService.apartmentId)
  }

 this.userService.getallapproveduserprofilesbyapartmentid2(approvedUsersParam).subscribe((res : any) => {


    this.rawApprovedUsersData = res.filter(item => {
      return item.userInfo.length != 0 && item.apartmentBlockUnitId != null;
    });

    this.approvedUsersData = this.rawApprovedUsersData;

    this.totalItems = this.approvedUsersData.length;
    if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }
    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isUserDataLoaded = true;


  },
  error => {
    console.log(error);
  }
  );
 }

 userDeleted(){
   this.getApprovedUsers();
   this.selectedTab = -1;
 }

  ngOnInit() {
    this.getApprovedUsers();
    let getTowerParam = {
      apartmentId: this.sessionService.apartmentId
    }

    //get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(getTowerParam).subscribe((res:any) => {
      this.towerList = res;
    });

    let params = {
      apartmentId: this.sessionService.apartmentId
    }

    this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res : any) => {


      this.totalUnits = res.length;

    },
    error => {
      console.log(error);
    }
    );

  }

}
